<style lang="css">
</style>

<template>
  <div id="main">
    <base-mulmenu @search="page">
      <router-link :to="newTask()">Êñ∞Âª∫‰ªªÂä°</router-link>
      <!-- <em @click="del()">Âà†Èô§</em> -->
    </base-mulmenu>
    <el-table :data="tableData" border @selection-change="checkAll">
      >
      <!-- <el-table-column type="selection" width="55"></el-table-column> -->
      <el-table-column prop="name" label="ÂêçÁß∞">
        <template slot-scope="scope">
          <i class="el-icon-loading" style="color:green;" v-if="scope.row.runstate==0"></i>
          <i class="el-icon-warning-outline" v-else></i>
          {{scope.row.name}}
        </template>
      </el-table-column>
      <el-table-column prop="cron" label="ÊâßË°åËßÑÂàô" width="150"></el-table-column>
      <el-table-column prop="url" label="Âú∞ÂùÄ" :min-width="100"></el-table-column>
      <el-table-column prop="method" label="ËØ∑Ê±ÇÊñπÊ≥ï" width="80"></el-table-column>
      <el-table-column label="Áä∂ÊÄÅÁ†Å">
        <template slot-scope="scope">{{scope.row.lastcode}}</template>
      </el-table-column>
      <el-table-column label="ÊúÄÂêé‰∏ÄÊ¨°ÈîôËØØ‰ø°ÊÅØ">
        <template slot-scope="scope">{{scope.row.lastresult}}</template>
      </el-table-column>
      <el-table-column label="ÊâßË°åÊó∂Èó¥">
        <template slot-scope="scope">{{scope.row.lasttime | formatTime}}</template>
      </el-table-column>
      <el-table-column label="ÊâßË°åÁªìÊùü">
        <template slot-scope="scope">{{scope.row.lasttimeend | formatTime}}</template>
      </el-table-column>
      <el-table-column label="ÊúâÊïàÊúü">
        <template slot-scope="scope">{{scope.row.expire_time | formatTime}}</template>
      </el-table-column>
      <el-table-column label="Êìç‰Ωú">
        <template slot-scope="scope">
          <el-button type="primary" @click="edit(scope.row.id)">ÁºñËæë</el-button>
          <el-dropdown type="primary" @command="handleCommand">
            <el-button type="primary">
              Êõ¥Â§ö
              <i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item
                :command="formatCommand('stop',scope.row)"
                v-if="scope.row.runstate==0"
              >ÂÅúÊ≠¢</el-dropdown-item>
              <el-dropdown-item :command="formatCommand('start',scope.row)" v-else>ÂêØÂä®</el-dropdown-item>
              <el-dropdown-item :command="formatCommand('buy',scope.row, scope.$index)">Áª≠Ë¥π</el-dropdown-item>
              <el-dropdown-item :command="formatCommand('del',scope.row)">Âà†Èô§</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </template>
      </el-table-column>
    </el-table>
    <div style="margin-top:30px;text-align:right;margin-right:20px;">
      <el-pagination
        background
        layout="prev, pager, next"
        :total="total"
        :page-size="apiParams.mp"
        @current-change="currentChange"
        v-if="total>apiParams.mp"
      ></el-pagination>
    </div>
  </div>
</template>

<script>
import baseMixin from '../mixin/index.js';
import buyTask from './form/buyTask';

export default {
  mixins: [baseMixin],
  data() {
    return {
      model: 'task'
    };
  },
  async mounted() {
    this.$on('updateData', (data) => {
      this.$set(this.tableData[data.index], data.key, data.value);
    });
  },
  methods: {
    before() {
      this.apiParams.groupid = this.$route.params.groupid;
    },
    newTask() {
      return '/usercenter/taskgroup/task/' + this.$route.params.groupid + '/edit';
    },
    async stopTask(row) {
      const doc = await this.$tools.httpAgent('/task/stop', 'get', {
        id: row.id
      });
      if (doc.code === 200) {
        row.runstate = 1;
        this.$layer.msg('ÂÅúÊ≠¢ÊàêÂäü');
      }
    },
    async startTask(row) {
      const doc = await this.$tools.httpAgent('/task/start', 'get', {
        id: row.id
      });
      if (doc.code === 200) {
        row.runstate = 0;
        this.$layer.msg('ÂºÄÂêØÊàêÂäü');
      }
    },
    buyTask(row = {}, index) {//Áª≠Ë¥π
      this.$layer.iframe({
        title: '',
        content: {
          content: buyTask,
          parent: this,
          data: {
            taskInfo: row,
            index: index
          }
        },
        area: ['500px', '250px']
      });
    },
    formatCommand(type, row, index = -1) {
      return {
        type,
        row,
        index
      }
    },
    handleCommand(command) {
      switch (command.type) {
        case 'del':
          this.del(command.row);
          break;
        case 'stop':
          this.stopTask(command.row);
          break;
        case 'start':
          this.startTask(command.row);
          break;
        case 'buy':
          this.buyTask(command.row, command.index);
          break;
      }
    }
  }
};
</script>
